/* src/styles/main.css */

*{
  box-sizing: border-box;
}


body {
    font-family: Arial, sans-serif;
  }

  h1 {
    color: #333;
  }
  
  form {
    margin-bottom: 20px;
  }
  
  input, textarea {
    display: block;
    margin-bottom: 10px;
    padding: 10px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 10px;
  }
  
  button:hover {
    background-color: #0056b3;
  }

  /* src/components/Header.css */
nav ul {
  list-style-type: none;
}

nav ul li {
  display: inline;
  margin-right: 10px;
}

nav ul li button {
  background: none;
  border: none;
  color: blue;
  text-decoration: underline;
  cursor: pointer;
  font-size: inherit;
}

.post{
  margin-top: 20px;
  background-color: #e1dfdf;
  padding: 0px 6px ;
  border-radius: 6px;
}

header{
  text-align: center;
  background-color: #6d6b6b;
  padding: 10px;
}

main{
  max-width: 700px;
  margin: auto;
  min-height: 80vh;
  padding: 10px 0px;
}
  

footer{
  text-align: center;
  background-color: #6d6b6b;
  padding: 10px;


}